import React, { useRef, useEffect } from 'react';
import type { ColorVisionSettings } from '../types/color';
import { simulateDeutanPoly, correctForDeutan, type RGB8 } from '../utils/deutan-simulator';

interface ColorCirclesProps {
  colorVisionSettings: ColorVisionSettings;
  width?: number;
  height?: number;
}

/**
 * RGBå††ã‚’é‡ã­ã¦è¡¨ç¤ºã™ã‚‹ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆ
 * å„å††ã®é€æ˜åº¦ã¯èª¿æ•´å€¤ã«å¿œã˜ã¦å¤‰åŒ–ã—ã€è‰²è¦šã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³ãƒ»è£œæ­£ã‚’é©ç”¨
 */
export const ColorCircles: React.FC<ColorCirclesProps> = ({
  colorVisionSettings,
  width = 400,
  height = 400,
}) => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  
  // RGBèª¿æ•´å€¤ï¼ˆå›ºå®šå€¤ï¼‰
  const adjustment = { red: 100, green: 100, blue: 100 };

  // è‰²å¤‰æ›ã‚’é©ç”¨ã™ã‚‹é–¢æ•°
  const applyColorTransform = (rgb: RGB8): RGB8 => {
    let transformedRgb = rgb;
    
    // ã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³ã‚’é©ç”¨
    if (colorVisionSettings.simulation.useSimulation && 
        colorVisionSettings.simulation.simulationType === 'deuteranopia') {
      transformedRgb = simulateDeutanPoly(
        transformedRgb, 
        colorVisionSettings.simulation.severity
      );
    }
    
    // è£œæ­£ã‚’é©ç”¨
    if (colorVisionSettings.correction.useCorrection && 
        colorVisionSettings.correction.correctionType === 'deuteranopia') {
      transformedRgb = correctForDeutan(
        transformedRgb, 
        colorVisionSettings.correction.strength
      );
    }
    
    return transformedRgb;
  };

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    // Canvas ã‚’ã‚¯ãƒªã‚¢
    ctx.clearRect(0, 0, width, height);

    // ã‚¢ãƒ³ãƒã‚¨ã‚¤ãƒªã‚¢ã‚·ãƒ³ã‚°ã‚’æœ‰åŠ¹åŒ–
    ctx.imageSmoothingEnabled = true;

    const centerX = width / 2;
    const centerY = height / 2;
    const radius = Math.min(width, height) / 3;

    // åŸºæº–è‰²ã‹ã‚‰å¤‰æ›ã•ã‚ŒãŸè‰²ã‚’è¨ˆç®—
    const baseRed: RGB8 = [255, 0, 0];
    const baseGreen: RGB8 = [0, 255, 0];
    const baseBlue: RGB8 = [0, 0, 255];
    
    const transformedRed = applyColorTransform(baseRed);
    const transformedGreen = applyColorTransform(baseGreen);
    const transformedBlue = applyColorTransform(baseBlue);

    // å††ã®é…ç½®ï¼ˆæ­£ä¸‰è§’å½¢ã®é ‚ç‚¹ã€ã‚ˆã‚Šè¿‘ãé…ç½®ï¼‰
    const circles = [
      {
        color: 'red' as const,
        x: centerX,
        y: centerY - radius * 0.3,
        intensity: adjustment.red,
        transformedColor: transformedRed,
      },
      {
        color: 'green' as const,
        x: centerX - radius * 0.4,
        y: centerY + radius * 0.2,
        intensity: adjustment.green,
        transformedColor: transformedGreen,
      },
      {
        color: 'blue' as const,
        x: centerX + radius * 0.4,
        y: centerY + radius * 0.2,
        intensity: adjustment.blue,
        transformedColor: transformedBlue,
      },
    ];

    // ã‚°ãƒ­ãƒ¼ãƒãƒ«ã‚³ãƒ³ãƒã‚¸ãƒƒãƒˆã‚ªãƒšãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³ã‚’è¨­å®šï¼ˆè‰²ã®æ··åˆï¼‰
    ctx.globalCompositeOperation = 'screen';

    // å„å††ã‚’æç”»
    circles.forEach(circle => {
      ctx.save();
      
      // èª¿æ•´å€¤ã«åŸºã¥ãé€æ˜åº¦ã‚’é©ç”¨
      ctx.globalAlpha = circle.intensity / 100;

      // ã‚°ãƒ©ãƒ‡ãƒ¼ã‚·ãƒ§ãƒ³ã‚’ä½œæˆ
      const gradient = ctx.createRadialGradient(
        circle.x,
        circle.y,
        0,
        circle.x,
        circle.y,
        radius
      );

      // å¤‰æ›ã•ã‚ŒãŸè‰²ã‚’ä½¿ç”¨ã—ã¦ã‚°ãƒ©ãƒ‡ãƒ¼ã‚·ãƒ§ãƒ³ã‚’è¨­å®š
      const [r, g, b] = circle.transformedColor;
      
      gradient.addColorStop(0, `rgba(${r}, ${g}, ${b}, 1)`);
      gradient.addColorStop(1, `rgba(${r}, ${g}, ${b}, 0)`);

      // å††ã‚’æç”»
      ctx.beginPath();
      ctx.arc(circle.x, circle.y, radius, 0, Math.PI * 2);
      ctx.fillStyle = gradient;
      ctx.fill();
      ctx.restore();
    });

    // èƒŒæ™¯ã«ç™½ã‚’è¿½åŠ ï¼ˆæ··åˆçµæœã‚’è¦‹ã‚„ã™ãã™ã‚‹ãŸã‚ï¼‰
    ctx.globalCompositeOperation = 'destination-over';
    ctx.fillStyle = '#FFFFFF';
    ctx.fillRect(0, 0, width, height);

  }, [colorVisionSettings, width, height]);

  return (
    <div style={{
      position: 'relative',
      display: 'inline-block',
      borderRadius: '0.75rem',
      overflow: 'hidden',
      boxShadow: '0 10px 15px -3px rgb(0 0 0 / 0.1)',
      background: '#FFFFFF',
      padding: '1.5rem'
    }}>
      <canvas
        ref={canvasRef}
        width={width}
        height={height}
        style={{
          display: 'block',
          borderRadius: '0.5rem',
          maxWidth: '100%',
          height: 'auto'
        }}
        aria-label="RGBè‰²èª¿æ•´ã®è¦–è¦šåŒ–"
      />
      {colorVisionSettings.simulation.showSimulationInfo && (
        <div style={{
          marginTop: '0.5rem',
          padding: '0.5rem',
          background: '#f0f9ff',
          borderRadius: '0.25rem',
          textAlign: 'center',
          border: '1px solid #bfdbfe'
        }}>
          <span style={{
            fontSize: '0.75rem',
            color: '#374151',
            fontWeight: '500'
          }}>
            {colorVisionSettings.simulation.useSimulation && (
              <>
                {colorVisionSettings.simulation.simulationType === 'deuteranopia' && 'ãƒ‡ãƒ¥ãƒ¼ã‚¿ãƒ³'}
                {colorVisionSettings.simulation.simulationType === 'protanopia' && 'ãƒ—ãƒ­ã‚¿ãƒ³'}
                {colorVisionSettings.simulation.simulationType === 'tritanopia' && 'ãƒˆãƒªã‚¿ãƒ³'}
                ã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³ ({(colorVisionSettings.simulation.severity * 100).toFixed(0)}%)
              </>
            )}
            {colorVisionSettings.correction.useCorrection && (
              <>
                {colorVisionSettings.simulation.useSimulation && ' | '}
                è£œæ­£ãƒ•ã‚£ãƒ«ã‚¿ãƒ¼ ({(colorVisionSettings.correction.strength * 100).toFixed(0)}%)
              </>
            )}
          </span>
        </div>
      )}
    </div>
  );
};